<template>
	<div class="about">
		<div class="carousel">
			<img :src="carousel">
		</div>

		<div class="c">
			<div>泰璞健康由泰璞日本与日本高端医疗机构合作，是集日本健康咨询与旅游咨询一体的服务机构，为您提供专业、全面、定制化医疗咨询服务。</div>
			<p>泰璞（上海）电子商务股份有限公司成立于2008年，总部位于上海，在东京、香港、北京、青岛等地均设有分公司。</p>
		</div>

		<div class="companys mb">
			<div class="company-item" :key="item.id" v-for="item in companyData">
				<div class="company-cover">
					<img :src="item.headImg">
				</div>
				<div class="company-meta">
					<div class="title">{{item.title}}</div>
					<p>{{item.address}}</p>
				</div>
			</div>
		</div>

		<div class="tp-artical tp-artical-server">
			<div class="header-name">
				<div class="zh">服务项目</div>
			</div>

			<div class="sv">
				<div class="e" @click="() => $router.push({ path: 'meal'})"></div>
			</div>
		</div>

		<div class="tp-artical tp-artical-hospital">
			<div class="header-name">
				<div class="zh">合作医院</div>
			</div>

			<div class="hospitals">
				<div class="hospital-item mb" :key="item.id" v-for="item in hospitalData">
					<div class="hospital-title"><i class="bar"></i> {{item.title}}</div>
					<div class="hospital-meta">
						<div class="cover">
							<img :src="item.headLogo">
							<span></span>
							<img :src="logo">
						</div>

						<p class="description">{{item.description}}</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'about',
		data() {
			return {
				companyData: [],
				hospitalData: [],
				carousel: `${process.env.BASE_URL}images/about/about_carousel.png`,
				logo: `${process.env.BASE_URL}images/logo.png`
			}
		},
		created() {
			fnCompany(this);
			fnHospital(this);
		}
	}

	function fnCompany(that, params) {
		that.$request.company(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.companyData = result.list;
			}
		})
	}

	function fnHospital(that, params) {
		that.$request.hospital(params, response => {
			if (response.data.code === 1) {
				let result = response.data.data;
				that.hospitalData = result.list;
			}
		})
	}
</script>

<style lang="scss">
	@import './../styles/about.scss'
</style>